import axios from "axios";
import { BACKEND_URL_ENDPOINT, GET_ALL_CHANNELS_API } from "../constants/backend";

import {
  CREATE_CHANNEL_REQUEST,
  CREATE_CHANNEL_SUCCESS,
  CREATE_CHANNEL_FAILURE,
  LIST_CHANNEL_REQUEST,
  LIST_CHANNEL_SUCCESS,
  LIST_CHANNEL_FAILURE,
  JOIN_CHANNEL_REQUEST,
  JOIN_CHANNEL_SUCCESS,
  JOIN_CHANNEL_FAILURE,
  DETAILS_CHANNEL_REQUEST,
  DETAILS_CHANNEL_SUCCESS,
  DETAILS_CHANNEL_FAILURE,
} from "../constants/channelActionConstants";

export const createChannel = () => async (dispatch) => {
  try {
    dispatch({
      type: CREATE_CHANNEL_REQUEST,
    });
    const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.sessionToken}`,
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH",
      },
    };
    console.log("Inside create channel function");
    await axios
      .post(BACKEND_URL_ENDPOINT + "channels/", config)
      .then((res) => {
        if (res.status === 200) {
          dispatch({
            type: CREATE_CHANNEL_SUCCESS,
            payload: res.data.message,
          });
        } else {
          dispatch({
            type: CREATE_CHANNEL_FAILURE,
            payload: res.data.message,
          });
        }
      });
  } catch (err) {
    dispatch({
      type: CREATE_CHANNEL_FAILURE,
      payload: err.response.data.message,
    });
  }
};

export const joinChannel =
  (userId = "") =>
  async (dispatch) => {
    try {
      dispatch({
        type: JOIN_CHANNEL_REQUEST,
      });
      const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
      if (userId == "") {
        userId = userInfo._id;
      }
      const config = {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${userInfo.sessionToken}`,
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH",
        },
      };
      await axios
        .get(BACKEND_URL_ENDPOINT + "channels/user/" + userId, config)
        .then((res) => {
          console.log(res);
          if (res.status === 200) {
            dispatch({
              type: JOIN_CHANNEL_SUCCESS,
              payload: res.data.data,
            });
          } else {
            dispatch({
              type: JOIN_CHANNEL_FAILURE,
              payload: res.data.message,
            });
          }
        });
    } catch (err) {
      dispatch({
        type: JOIN_CHANNEL_FAILURE,
        payload: err.response.data.message,
      });
    }
  };

  export const listChannels = () => async (dispatch) => {
  try {
    dispatch({
      type: LIST_CHANNEL_REQUEST,
    });

    const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH",
      },
    };

    await axios
      .get(GET_ALL_CHANNELS_API, config)
      .then((res) => {
        if (res.status === 200) {
          dispatch({
            type: LIST_CHANNEL_SUCCESS,
            payload: res.data.data,
          });
        } else {
          dispatch({
            type: LIST_CHANNEL_FAILURE,
            payload: 'Not able to fetch the channels',
          });
        }
      });
  } catch (err) {
    dispatch({
      type: LIST_CHANNEL_FAILURE,
      payload: 'Not abl to fetch the channels',
    });
  }
};

export const fetchChannelDetails = (channelId) => async (dispatch) => {
  try {
    dispatch({
      type: DETAILS_CHANNEL_REQUEST,
    });
    const userInfo = JSON.parse(sessionStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.sessionToken}`,
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH",
      },
    };
    await axios
      .put(BACKEND_URL_ENDPOINT + "channels/" + channelId, config)
      .then((res) => {
        if (res.status === 200) {
          dispatch({
            type: DETAILS_CHANNEL_SUCCESS,
            payload: res.data.message,
          });
        } else {
          dispatch({
            type: DETAILS_CHANNEL_FAILURE,
            payload: res.data.message,
          });
        }
      });
  } catch (err) {
    dispatch({
      type: DETAILS_CHANNEL_FAILURE,
      payload: err.response.data.message,
    });
  }
};
