import React, { useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import Channel from "../components/Channels";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listChannels } from "../actions/channelActions";
import AlertMessage from "../components/AlertMessage";
import Spinner from "react-bootstrap/Spinner";

const ChannelListPage = () => {
  const [channelList, setChannelList] = useState();

  const dispatch = useDispatch();
  
  const listOfChannels = useSelector((state) => state.channelList);
  console.log("Channels are:==>"+ listOfChannels);
  const { loading, success, error, channels } = listOfChannels;
  

  useEffect(() => {
    dispatch(listChannels());
    if (channels && channels.length > 0) {
      setChannelList(channels);
    }
  }, [dispatch]);

  useEffect(() => {
    if (channels && channels.length > 0) {
      setChannelList(channels);
    }
  }, [success]);

  return (
    <>
      {loading && <Spinner animation="grow" />}
      {channelList && channelList.length === 0 && (
        <AlertMessage variant="info" message="No channels to display" />
      )}
      {channelList && (
        <Row>
          {channelList.map((channel) => (
            <Col key={channel._id} md={6} sm={12} lg={4}>
              <Channel channel={channel} />
            </Col>
          ))}
        </Row>
      )}
    </>
  );
};

export default ChannelListPage;
